})
export default class Detail extends Vue implements DetailState {
	goodDetail = {} as GoodDetailInfo
	norms = {} as ApiSkuType
	getNorms = {} as ApiSkuType
	buyShow = false
	show = false
	paraShow = false
	timeData: TimeDataType = {
		hours: [],
		seconds: [],
		minutes: []
	}
	state = ''
	version = 'V1.8'
	shareVisible = false
	shareImgUrl = ''
	imgUrl = ''
	cardImgUrl = ''
	cardFlag = true
	showAll = false
	goodId = ''
	goodsVideoUrl = ''
	videoImg = ''
	autoplay = true
	current: number = 1
	videoContext!: UniApp.VideoContext
	countDetail: InterCountDetail = {
		status: '',
		toBeginTime: 0,
		toEndTime: 0,
		remainTime: 0
	}
	remainTime = 0
	authType = false
	evaluateItem = {} as EvaluateItemType
	evaluateOverview: EvaluateOverviewType[] = []
	shopCarNum = 0
	clickShare = false
	rebateRatio = 0
	isRebate = false
	hideGoodBox = true
