	/**
	 * @LastEditors: chuyinlong
	 * @description: 规格选择
	 * @param {ApiSkuType} check
	 */
	checkNorms(check: ApiSkuType) {
		this.goodDetail.skuStocks.forEach((item) => {
			item.getType = false
			if (item.id === check.id) {
				item.getType = true
			}
		})
		this.setData({
			getNorms: check,
			goodDetail: Object.assign({}, this.goodDetail)
		})
